/* Box Model Hack */
* {
    box-sizing: border-box;
}

:root {
    --primary-color: #f1f1f1;
    --secondary-color: rgb(14, 92, 114);
}

body {
    margin: 0;
    font-family: "Barlow", sans-serif;
    font-size: 62.5%;
    background-color: var(--primary-color);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

a:hover,
.navList li a:hover {
    color: var(--secondary-color);
}


/*
 ***********
    Header
 ***********
 */

header {
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
}

.nav {
    display: flex;
    align-items: center;
}

.navHeadingContainer {
    margin: 1rem auto 0 4rem;
    font-size: 1.5rem;
    color: black;
    text-shadow: 0 0 4px var(--primary-color);
}

.navList {
    display: flex;
    width: 20%;
    margin: 1rem 4rem 0 auto;
    justify-content: flex-end;
    text-shadow: 0 0 4px var(--primary-color);
}

.navListItem a {
    font-family: "Barlow", sans-serif;
    color: black;
    padding-left: 1rem;
    font-size: 1.5rem;
    font-weight: 600;
}


.navListItem a:visited {
    color: black;
}

a:visited {
    color: var(--secondary-color);
}




/******************
       Home Page
/******************/

.wrapper {
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
    perspective: 10px;
}

.welcome {
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    transform-style: preserve-3d;
    z-index: -1;
}

.welcomeBackground {
    transform: translateZ(-10px) scale(2.2);
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: -1;
}

.welcomeMsg {
    margin: 0 auto;
    width: 90%;
    font-size: 6rem;
    text-align: end;
    display: flex;
    flex-direction: column;
    color: var(--primary-color);
    text-shadow: 0 0 5px black;
}

.mainSection {
    width: auto;
    margin: 0;
    height: auto;
    background: var(--primary-color);
    margin-bottom: 8rem;
}

.mainContainer {
    width: 80%;
    margin: auto;
    text-align: center;
    padding-top: 4rem;
}

.mainHeading {
    font-size: 7rem;
    color: var(--secondary-color);
    padding-bottom: 5rem;
    text-shadow: -2px 2px 5px #aaa;
}

.mainIntro {
    font-size: 2.5rem;
    font-weight: 500;
    width: 80%;
    margin: auto;
}

.logo,
.logo a {
    color: var(--secondary-color);
}

/*
=====================
    User Home Page
======================
*/

.userProfileHeader {
    margin: 125px auto 0 auto;
    text-align: center;
    font-size: 1.75rem;
    position: relative;
}

.userProfileHeader h1 {
    margin-bottom: 10px;
}

.createNew,
.createNew:visited {
    font-size: .8em;
    border: 1px solid black;
    padding: 8px;
    background-color: white;
}

.createNew:hover {
    box-shadow: 0 0 5px 1px var(--secondary-color);
    border: none;
}

.cardContainer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 20px auto;
    width: 90%;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    padding: 20px 5px;
    font-size: 1.3rem;
    text-align: center;
    border: 1px solid black;
    box-shadow: 0 1px 5px -2px black;
}

.cardDate {
    margin-bottom: 10px;
}

.cardText {
    font-weight: bold;
}

.profileBtn:visited {
    color: inherit;
}

.cardBtnContainer {
    margin-top: 10px;
}

.emptyList {
    font-size: 2rem;
    margin-top: 2rem;
    text-align: center;
}

/*
 ==========================
    Create Workout Page
 ==========================
 */

.exerciseContainer {
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    margin-top: 100px;
}

.exerciseData {
    max-height: 75vh;
    overflow: auto;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.inputContainer {
    text-align: center;
    margin-bottom: 0.5rem;
}

.exercisesList {
    font-size: 1.5rem;
    margin: .8rem 1rem;
    font-weight: 400;
}

.searchList {
    display: grid;
    align-items: center;
    width: 100%;
    grid-template-columns: 1fr 1fr;
    text-align: left;
    margin: .5rem auto;
}

.exerciseBtn {
    padding: 8px;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    background-color: white;
    box-shadow: 0 0 1px 1px black;
    margin: 0 .3rem;
}

.exerciseBtn:hover {
    box-shadow: 0 0 2px 2px var(--secondary-color);
}

.exerciseDisplay h3 {
    font-size: 1.5rem;
    font-weight: 500;
    margin: 1rem 0;
    padding: 0;
}

.exerciseDisplay select {
    align-self: center;
    font-size: 1.2rem;
    width: fit-content;
    padding: 8px 5px;
    text-align: center;
    margin: .5rem;
    background-color: white;
    border: none;
    box-shadow: 0 0 1px 1px black;
}

.exerciseDisplay select:hover {
    cursor: pointer;
}

#todaysExercises {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 30%;
    margin-bottom: 1rem;
}

#todaysExerciseList {
    padding: 0;
}

#todaysExerciseList label {
    font-weight: 400;
}

.currentExercises {
    font-size: 1.35rem;
    font-weight: bold;
}

.todayHeader {
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    padding: 0;
    margin: 1rem;
}

.removeBtn {
    font-size: .8rem;
    font-weight: 400;
    padding: 5px;
    margin-top: 10px;
}

.exNumInput {
    width: 75px;
    margin-right: 10px;
}

.logBtn {
    width: fit-content;
    align-self: center;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}

/*
 ***********
    Footer
 ***********
 */

footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    margin-top: 1rem;
    /* border-top: 1px solid black; */
    box-shadow: 0 -.5px 7px;
    background-color: white;
    display: flex;
}

.quoteContainer {
    text-align: center;
    margin: 1rem;
}

.quote,
.quoteAuthor {
    font-size: 1.25rem;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

/*
==================
    Login Page
==================
 */
.loginPageContainer {
    display: flex;
    flex-direction: row;
}

.loginContainer,
.registerContainer {
    min-width: fit-content;
    background: var(--secondary-color);
    color: var(--primary-color);
    margin: 10rem auto 0 auto;
    border-radius: 20px;
    padding: 100px 100px;
    box-shadow: 0 0 10px #a1a0a0;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.loginHeading {
    font-size: 2rem;
    margin-bottom: .5em;
    text-align: center;
}

.loginForm {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.loginInput {
    width: 100%;
    text-align: center;
    margin: .5rem;
    padding: 8px;
    border: 1px solid black;
}


/*
====================
    Hamburger Menu
====================
 /*
 /* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 36px;
    top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: black;
    box-shadow: 0 0 4px var(--primary-color);
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
    background: var(--secondary-color);
    box-shadow: none;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
    height: 24px;
    width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
    background: var(--secondary-color);
}

/*
  Sidebar wrapper styles
  */
.bm-menu-wrap {
    position: fixed;
    height: 100%;
    top: 0;
    box-shadow: 0 0 5px #a1a1a1;
}

/* General sidebar styles */
.bm-menu {
    background: var(--primary-color);
    padding: 2.5em 1.5em 0;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
    fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
}

/* Individual item */
.bm-item {
    display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
    background: rgba(0, 0, 0, 0.3);
}

.menu-item {
    font-family: "Barlow", sans-serif;
    color: var(--secondary-color);
    font-size: 2rem;
    font-weight: 500;

}

.menu-item:hover {
    font-weight: bold;
}

.menu-item a {
    font-family: "Barlow", sans-serif;
}

@media screen and (max-width: 900px) {

    .navHeadingContainer {
        margin: 25px auto 0 1rem;
    }

    .welcomeBackground {
        transform: translateZ(-10px) scale(2);
    }

    .welcomeMsg {
        font-size: 2rem
    }

    .mainContainer {
        width: 95%;
        padding-top: 6rem;
    }

    .mainHeading {
        font-size: 3rem;
    }

    .mainIntro {
        font-size: 1.4rem;
    }

    .navHeadingContainer {
        margin: 25px auto 0 1rem;
        font-size: .8rem;
    }

    .welcomeBackground {
        transform: translateZ(-10px) scale(2);
    }

    .welcomeMsg {
        font-size: 2rem
    }

    .mainContainer {
        width: 95%;
        padding-top: 2rem;
    }

    .mainHeading {
        font-size: 3rem;
    }

    .mainIntro {
        font-size: 1.4rem;
    }

    .exerciseContainer {
        display: block;
    }

    .cardContainer {
        display: flex;
        flex-direction: column;
    }

    .searchList {
        grid-template-rows: 1fr 1fr;
        grid-template-columns: none;
    }

    .exerciseListBtnContainer {
        margin-bottom: .5rem;
    }

    .loginPageContainer {
        flex-direction: column;
    }

    .registerContainer {
        margin-top: .5rem;
    }

}